function formatAccount(x) {
  var acc = x;
  var _accStart = acc.slice(0,6);
  var _accEnd = acc.slice(-4);
  var result = _accStart+"..."+_accEnd;
  return(result);
}
function formatTimestamp(_num) {
  var num = _num;
  if(num>=63072000){num=Math.floor(num/31536000)+" yrs ago";}else
  if(num>=31536000){num=Math.floor(num/31536000)+" yr ago";}else
  if(num>=5184000){num=Math.floor(num/2592000)+" mths ago";}else
  if(num>=2592000){num=Math.floor(num/2592000)+" mth ago";}else
  if(num>=1209600){num=Math.floor(num/604800)+" wks ago";}else
  if(num>=604800){num=Math.floor(num/604800)+" wk ago";}else
  if(num>=172800){num=Math.floor(num/86400)+" days ago";}else
  if(num>=86400){num=Math.floor(num/86400)+" day ago";}else
  if(num>=7200){num=Math.floor(num/3600)+" hrs ago";}else
  if(num>=3600){num=Math.floor(num/3600)+" hr ago";}else
  if(num>=120){num=Math.floor(num/60)+" mins ago";}else
  if(num>=60){num=Math.floor(num/60)+" min ago";}else{num=num+" secs ago";}
  return(num);
}
function formatCurrency(_decis,_num) {
  var result;
  var wholes = Math.floor(_num/_decis);
  var decis = _num-(wholes*_decis);
  if(wholes > 9999999){
    if(decis === 0){decis='0';}
    wholes = parseInt(wholes).toLocaleString();
    result = wholes;
  }else
  if(wholes > 999999){
    decis = Math.floor(decis/(_decis/100));
    if(decis > 0){decis = '0'+decis;}else
    if(decis === 0){decis='00';}
    wholes = parseInt(wholes).toLocaleString();
    result = wholes+'.'+decis;
  }else
  if(wholes > 99999){
    decis = Math.floor(decis/(_decis/1000));
    if(decis > 99){}else
    if(decis > 9){decis = '0'+decis;}else
    if(decis > 0){decis = '00'+decis;}
             else{decis = '000';}
    wholes = parseInt(wholes).toLocaleString();
    result = wholes+'.'+decis;
  }else
  if(wholes > 9999){
    decis = Math.floor(decis/(_decis/10000));
    if(decis > 999){}else
    if(decis > 99){decis ='0'+decis;}else
    if(decis > 9){decis = '00'+decis;}else
    if(decis > 0){decis = '000'+decis;}
             else{decis = '0000';}
    wholes = parseInt(wholes).toLocaleString();
    result = wholes+'.'+decis;
  }else
  if(wholes > 999){
    decis = Math.floor(decis/(_decis/100000));
    if(decis > 9999){}else
    if(decis > 999){decis ='0'+decis;}else
    if(decis > 99){decis = '00'+decis;}else
    if(decis > 9){ decis = '000'+decis;}else
    if(decis > 0){ decis = '0000'+decis;}
              else{decis = '00000';}
    wholes = parseInt(wholes).toLocaleString();
    result = wholes+'.'+decis;
  }else
  if(wholes > 99){
    decis = Math.floor(decis/(_decis/1000000));
    if(decis > 99999){}else
    if(decis > 9999){decis='0'+decis;}else
    if(decis > 999){decis= '00'+decis;}else
    if(decis > 99){ decis= '000'+decis;}else
    if(decis > 9){  decis= '0000'+decis;}else
    if(decis > 0){  decis= '00000'+decis;}
              else{ decis= '000000';}
    wholes = parseInt(wholes).toLocaleString();
    result = wholes+'.'+decis;
  }else
  if(wholes > 9){
    decis = Math.floor(decis/(_decis/10000000));
    if(decis > 999999){}else
    if(decis > 99999){decis='0'+decis;}else
    if(decis > 9999){decis= '00'+decis;}else
    if(decis > 999){ decis= '000'+decis;}else
    if(decis > 99){  decis= '0000'+decis;}else
    if(decis > 9){   decis= '00000'+decis;}else
    if(decis > 0){   decis= '000000'+decis;}
              else{ decis= '0000000';}
    wholes = parseInt(wholes).toLocaleString();
    result = wholes+'.'+decis;
  }else{
    decis = Math.floor(decis/(_decis/100000000));
    if(decis > 9999999){}else
    if(decis > 999999){decis='0'+decis;}else
    if(decis > 99999){decis= '00'+decis;}else
    if(decis > 9999){ decis= '000'+decis;}else
    if(decis > 999){  decis= '0000'+decis;}else
    if(decis > 99){   decis= '00000'+decis;}else
    if(decis > 9){    decis= '000000'+decis;}else
    if(decis > 0){    decis= '0000000'+decis;}
              else{ decis= '00000000';}
    wholes = parseInt(wholes).toLocaleString();
    result = wholes+'.'+decis;
  }
  return(result);
}
