function formatToken(tok) {
  var logo;
  /*STABLES*/
  if(tok=='DAI'){logo=logo_DAI;}else
  if(tok=='VAI'){logo=logo_VAI;}else
  if(tok=='USDC'){logo=logo_USDC;}else
  if(tok=='USDT'){logo=logo_USDT;}else
  if(tok=='BUSD'){logo=logo_BUSD;}else
  if(tok=='HUSD'){logo=logo_HUSD;}else
  /*CHAINS*/
  if(tok=='BTC'){logo=logo_BTC;}else
  if(tok=='WBTC'){logo=logo_BTC;}else
  if(tok=='BTCB'){logo=logo_BTC;}else
  if(tok=='ETH'){logo=logo_ETH;}else
  if(tok=='WETH'){logo=logo_ETH;}else
  if(tok=='BETH'){logo=logo_ETH;}else
  if(tok=='BNB'){logo=logo_BNB;}else
  if(tok=='WBNB'){logo=logo_BNB;}else
  if(tok=='MATIC'){logo=logo_MATIC;}else
  if(tok=='WMATIC'){logo=logo_MATIC;}else
  if(tok=='FTM'){logo=logo_FTM;}else
  /*DEXS*/
  if(tok=='UNI'){logo=logo_UNI;}else
  if(tok=='SUSHI'){logo=logo_SUSHI;}else
  if(tok=='CAKE'){logo=logo_CAKE;}else
  if(tok=='QUICK'){logo=logo_QUICK;}else
  /*MISC*/
  if(tok=='COMP'){logo=logo_COMP;}else
  if(tok=='XVS'){logo=logo_XVS;}else{logo=logo_FTM;}
  return(logo);
}

