  async checkBet(){/*COMPLETED*/
    if((this.state.Dapp_Contract!=null)&&(this.state.UID!=null)&&(menuOn==2)){
      var amt=document.getElementById("INPUT2").value;
      var timeleft2=TN_ALL;
      if(document.getElementById("INPUT2").value!==''){
        amt=(amt*this.state.decimals);
        var cur=this.state.pending2;
        var min=this.state.minimum2;
        var power=this.state.usersBalance2;
        var num=document.getElementById("INPUT3").value;
        if((cur >= min)&&(timeleft2 == 0)&&(cur >= amt)&&(amt >= min)&&(power > 0)&&(amt <= power)&&(parseInt(num)>=1)&&(parseInt(num)<=100)&&(num!="")){
          document.getElementById("WARNbet").className="warnTxtB";
          document.getElementById("INPUT2").className="labelData2a";
          this.setState({PenCol:"abtn greend"});
          this.setState({drawWinnerBtn_class:"btn btn2aF btnPurp",drawWinnerBtn_open:false});
          if(power == 0){this.setState({BalCol:"grayd"});}else{this.setState({BalCol:"abtn orangd"});}
        }else{
        
          if(amt < min){document.getElementById("WARNbet").className="warnTxt rndPnd";}
          else{document.getElementById("WARNbet").className="warnTxtB";}

          if(amt > power){this.setState({BalCol:"abtn redd rndPnd"});}else{
            if(power == 0){this.setState({BalCol:"grayd"});}else{this.setState({BalCol:"abtn orangd"});}
          }

          if(cur == 0){this.setState({PenCol:"abtn grayd"});}else
          if(cur < min){this.setState({PenCol:"abtn orangd"});}else 
          if(amt > cur){this.setState({PenCol:"abtn redd rndPnd"});}else{this.setState({PenCol:"abtn greend"});}

          if(timeleft2>0){this.setState({TimeFlash:"rndPnd"});}else{this.setState({TimeFlash:""});}

          if(num==""){
            document.getElementById("INPUT3").style.borderColor="red";
            document.getElementById("INPUT3").className="labelData2b rndPnd";
            
          
          }
          
          document.getElementById("INPUT2").className="labelData2a redd";
          
          this.setState({drawWinnerBtn_class:"btn btn2a btnGray",drawWinnerBtn_open:true});
        }
      }else{
        if(timeleft2>0){this.setState({TimeCol:"redd"});}
        document.getElementById("WARNbet").className="warnTxtB";
        document.getElementById("INPUT2").className="labelData2a";
        this.setState({TimeFlash:""});
        this.setState({BalCol:"abtn orangd"});
        this.setState({PenCol:"abtn greend"});
        this.setState({drawWinnerBtn_class:"btn btn2a btnGray",drawWinnerBtn_open:true});
      }
    }
  }
  async checkDepo(){
    if((this.state.Dapp_Contract!=='undefined')&&(menuOn==2)){
      var amt=document.getElementById("INPUT1").value;
      var cur=this.state.walletBalance2;
      var min=this.state.minimum2;
      if(cur==0){this.setState({walCol:"abtn grayd"});}else if(cur > min){this.setState({walCol:"abtn orangd"});}else{this.setState({walCol:"abtn redd"});}
      if(document.getElementById("INPUT1").value!==''){
        amt=(amt*this.state.decimals);
        if((amt >= min)&&(amt <= cur)){
          document.getElementById("WARNdepo").className="warnTxtB";
          document.getElementById("INPUT1").className="labelData2a";
          this.setState({depositBtn_class:"btn btn2 btnGreen",depositBtn_open:false});
        }else{
          if(amt < min){
            document.getElementById("WARNdepo").className="warnTxt rndPnd";
          }else{
            document.getElementById("WARNdepo").className="warnTxtB";
            this.setState({walCol:"abtn redd rndPnd"});
          }
          document.getElementById("INPUT1").className="labelData2a redd";
          this.setState({depositBtn_class:"btn btn2 btnGray",depositBtn_open:true});
        }
      }else{
        document.getElementById("WARNdepo").className="warnTxtB";
        document.getElementById("INPUT1").className="labelData2a";
        this.setState({depositBtn_class:"btn btn2 btnGray",depositBtn_open:true});
      }
  
    }
  }
