  async autoUpdateB() {
    setTimeout(()=>{this.autoUpdateB()}, 1000);
    if(menuOn==2){
      //Update Timer
      if(TN_ALL>0){
        var dis_TN_Sec;var dis_TN_Min;var dis_TN_Hr;var timeleft;var TimeCol2;
        if(TN_Sec==0){
          TN_Sec=59;
          if(TN_Min==0){
            TN_Min=59;
            if(TN_Hr==0){TN_Min=0;}else{TN_Hr-=1;}
          }else{TN_Min-=1;}
        }else{TN_Sec-=1;}
        TN_ALL-=1;
        if(TN_ALL>0){
          if(TN_Sec<10){dis_TN_Sec="0"+TN_Sec;}else{dis_TN_Sec=TN_Sec;}
          if(TN_Min<10){dis_TN_Min="0"+TN_Min;}else{dis_TN_Min=TN_Min;}
          if(TN_Hr<10){dis_TN_Hr="0"+TN_Hr;}else{dis_TN_Hr=TN_Hr;}
          TimeCol2="redd";
          timeleft=<b>{dis_TN_Hr}:{dis_TN_Min}:{dis_TN_Sec}</b>;
        }else{
          this.setState({TimeFlash:""});
          TimeCol2="greend";
          timeleft=<b>00:00:00</b>;
          this.checkBet();
        }
        this.setState({timeleft:timeleft,timeleft2:TN_ALL,TimeCol:TimeCol2});
      }
    }
    
    if(menuOn==1){
      if(timeNUM2<60){timeNUM2=timeNUM2+1;}else{timeNUM2=0;}
      if(timeNUM2==60){// 60 SECOND ALARM
        if(menuOpen==false){
          console.log("autoUpdate H");
          this.loadBlockchainData();
        }else{
          console.log("autoUpdate-blocked-busySys");
        }
      }
    }else{
      if(timeNUM<10){timeNUM=timeNUM+1;}else{timeNUM=0;}
      if(timeNUM==10){// 10 SECOND ALARM
        if(menuOpen==false){
          console.log("autoUpdate O");
          this.loadBlockchainData();
        }else{
          console.log("autoUpdate-blocked-busySys");
        }
      }
    }
  }
