  async newCategory() {
    if(this.state.Base_Contract!=='undefined'){
      var estGas=await this.getGasPrice();const blockFee = this.state.web3.utils.toWei(estGas, "gwei");
      try{
        await this.state.Base_Contract.methods.newCategory().send({maxPriorityFeePerGas: blockFee,from: this.state.account})
        .on('transactionHash', (hash) => {})
        .on('confirmation', (confirmationNumber) => {})
        .on('error', (error, result) => {});
      }catch(e){console.log('Error, New Category: ',e)}
    }
  }
  async newContract(_Cat,_Addy,_Proto,_Name,_Taddy,_Caddy,_Num) {
    if(this.state.Base_Contract!=='undefined'){
      var Cat=_Cat.toString();
      var Addy=_Addy.toString();
      var Proto=this.state.web3.utils.asciiToHex(_Proto);
      var Name=this.state.web3.utils.asciiToHex(_Name);
      var Taddy=_Taddy.toString();
      var Caddy=_Caddy.toString();
      var Num=_Num.toString();
      var estGas=await this.getGasPrice();const blockFee = this.state.web3.utils.toWei(estGas, "gwei");
      try{
        await this.state.Base_Contract.methods.newContract(Cat,Addy,Proto,Name,Taddy,Caddy,Num).send({maxPriorityFeePerGas: blockFee,from: this.state.account})
        .on('transactionHash', (hash) => {})
        .on('confirmation', (confirmationNumber) => {})
        .on('error', (error, result) => {});
      }catch(e){console.log('Error, New Contract: ',e)}
    }
  }
